  color: #2e4369;
  font-weight: 600;
}

.tw-animated-map__transfer-container .tw-animated-map__transfer-label:after {
  position: absolute;
  right: -3px;
  top: 50%;
  display: block;
  width: 8px;
  height: 8px;
  content: " ";
  background-color: inherit;
  -webkit-transform: translateY(-50%) rotate(45deg);
  -ms-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
}

.tw-animated-map__transfer-container.fade-enter
  .tw-animated-map__transfer-label {
  opacity: 0;
  -webkit-transform: translateY(-50%) scale(0.94) translateX(10px);
  -ms-transform: translateY(-50%) scale(0.94) translateX(10px);
  transform: translateY(-50%) scale(0.94) translateX(10px);
}

.tw-animated-map__transfer-container.fade-enter-active
  .tw-animated-map__transfer-label {
  opacity: 1;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  -webkit-transition: 0.6s ease;
  -o-transition: 0.6s ease;
  transition: 0.6s ease;
  -webkit-transition-delay: 1s;
  -o-transition-delay: 1s;
  transition-delay: 1s;
}

.tw-animated-map__transfer-container.fade-exit
  .tw-animated-map__transfer-label {
  opacity: 1;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.tw-animated-map__transfer-container.fade-exit-active
